



































	import uniRate from "@/components/uni-rate/uni-rate.vue"
	var userLoginInfo;
	export default {
		components: {uniRate},
		data () {
			return { 
				content:'',
				locS:0,//物流
				serS:0,//服务
				goodsS:0,//宝贝
				goodsId:0,
				images:''
			}
		},
		onShow() {
			userLoginInfo = this.userLoginInfo('../order/order', 2);
		},
		onLoad(params) {
			console.log(params);
			this.goodsId = params.goods_id;	
			this.images = params.goods_image;
		},
		methods:{
			// 点击发表评价
			subComment(){
				uni.request({
					url: this.apiUrl + 'goods/addGoodsComment',
					method: 'POST',
					header: {
						'content-type': "application/x-www-form-urlencoded"
					},
					data: {
						goods_id:this.goodsId,
						uid:userLoginInfo.id,
						content:this.content,
						loc_score:this.locS,//物流分数
						ser_score:this.serR,//服务
						goods_score:this.goodsS,//商品
					},
					header: {
						'content-type': "application/x-www-form-urlencoded"
					},
					success: res => {
						if(res.data.code===0){
							uni.showToast({
								title: '评论成功',
								icon:'none',
								success: (res) => {
									setTimeout(function () {
										uni.navigateBack({
											delta:1
										});
									}, 800);
								}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//物流评分
			locRate(res){
				this.locS = res.value;				
			},
			// 服务评分
			serRate(res){
				this.serR = res.value;				
			},
			// 商品评分
			goodsRate(res){
				this.goodsS = res.value;				
			},
			
	}
}
